@use '../base/variables' as v;
@use '../base/mixins' as m;

.favoritos{
    background-color: v.$grisClaro;
    padding: 5rem 0;

    &__titulo{
        text-align: center;
        color: v.$secundario;
    }

    &__grid{
        @include m.tablet { 
            @include m.grid(2, 5rem)
        }
    }
    
}